import React, { Component } from 'react';

export default class Play extends Component {

    componentDidMount() {
        setInterval(function(){
            let windWidth = window.innerWidth;
            let windHeight = window.innerHeight;
            let catSize = windWidth / 14;
            let randomX = Math.floor(Math.random() * (windWidth - catSize));
            let randomY = Math.floor(Math.random() * (windHeight - catSize));
            var el = document.getElementById('cat');
            el.style.top = randomY + 'px';
            el.style.left = randomX + 'px';
        }, 1000);
    }
    

     

    render() {

        var catStyle = {
            opacity: this.props.catOpacity
        }

        return (
            <div className="play">
                <div className="play-area wbs-full-screen">
                        <div id="cat" className="the-cat" onClick={this.props.nextStep} style={catStyle}></div>
                </div>
                <div className="popup-message wbs-middle-center">
                    <div className="message-step">{this.props.gameStep}</div>
                    <div className="message-result">You found it</div>
                    <div className="message-joke">How you did that, you are superhero</div>
                    <div className="btn message-cta" onClick={this.props.nextStep}>Continue</div>
                </div> 
            </div>
        )
    }
}